let html = document.querySelector("#demo");
let style = document.querySelector("#style");

let string = `
/*你好，我是一名前端新人
*接下来我来演示一下我的前端功底
*首先我准备一个div
*/
#div1{
    
    width:200px;
    height:200px;
}
/*接下来我把div变成一个八卦图
*注意看好了
*首先，我把div变成一个圆
*/
#div1{
    border-radius:50%;
    box-shadow:0 0 3px rgba(0,0,0,0,5);
    border:1px solid black;
}
/*八卦是阴阳形成的
*一黑一白
**/
#div1{
    background: linear-gradient(90deg, rgba(255,255,255,1) 0%,
     rgba(6,74,164,1) 0%, rgba(255,255,255,1) 0%, 
     rgba(255,255,255,1) 50%, rgba(0,0,0,1) 50%);
}
/*加两个圆*/
#div1::before{
    width:100px;
    height:100px;
    left:50%;
    top:0;
    transform:translateX(-50%);
    background:#000;
    border-radius:50%;
    background: radial-gradient(circle, rgba(255,255,255,1) 0%,
     rgba(6,74,164,1) 0%, rgba(255,255,255,1) 0%, 
     rgba(255,255,255,1) 25%, rgba(0,0,0,1) 25%);
}
#div1::after{
    width:100px;
    height:100px;
    left:50%;
    bottom:0;
    transform:translateX(-50%);
    background:#fff;
    border-radius:50%;
    background: radial-gradient(circle, rgba(255,255,255,1) 0%,
     rgba(0,0,0,1) 0%, rgba(0,0,0,1) 25%, rgba(255,255,255,1) 25%, 
     rgba(255,255,255,1) 100%);
}


`;
//string = string.replace(/\n/g,"<br>")
let string2 = ''
console.log(string.length);
let n = -1;


//demo.innerHTML = string.substring(0,n);

let step = () =>{
    setTimeout(()=>{
        
        if(string[n] ==="\n"){
            string2 += "<br>";
        }else if(string[n]===" "){
           string2 += "&nbsp;";
        }else{
            string2 += string[n];
        }

        html.innerHTML =string2;
        style.innerHTML = string.substring(0,n);
        window.scrollTo(0,99999);
        html.scrollTo(0,99999);
        if(n < string.length-1){
            n +=1;
            step(); 
        }
       
    },10);

};
step();

